.root-play {
  min-width: 375px;
  width: 100%;
  height: 100vh;
  grid-template-rows: 1fr 8% 1fr;
  padding: 30px;
  display: grid;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@media (min-width: 450px) {
  .root-play {
    height: inherit;
    border: 4px solid #000;
    border-radius: 6px;
    justify-content: center;
    padding: 70px 55px;
  }
}

@media (min-width: 1150px) {
  .root-play {
    height: inherit;
    border: 6px solid #000;
    border-radius: 6px;
    justify-content: center;
    gap: 78px;
    padding: 30px 95px 5px;
  }
}

