.container {
  width: 170px;
  height: 170px;
  border-radius: 70%;
  flex-direction: column;
  place-self: center;
  padding: 25px;
  animation: 1.5s infinite pl1;
  display: flex;
  box-shadow: 0 0 #0004;
}

@keyframes pl1 {
  100% {
    box-shadow: 0 0 0 60px #0000;
  }
}

.counter {
  place-self: center;
}

.custom-plays, .custom-plays-reverse {
  align-self: center;
}

.container-choose {
  background-color: var(--color-empate);
  border: 8px solid #000;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  padding: 20px 35px;
  display: flex;
}

.choose-text {
  color: #fff;
  margin: 0;
  font-family: Odibee Sans;
  font-size: 2.8rem;
  font-weight: 400;
}

.progress-bar {
  width: 70%;
  height: 20px;
  background: linear-gradient(var(--color-principal) 0 0) 0 / 0% no-repeat #ddd;
  place-self: center;
  animation: 3.98s linear infinite p1;
}

@keyframes p1 {
  100% {
    background-size: 100%;
  }
}

@media (min-width: 1150px) {
  .progress-bar {
    width: 70%;
    height: 30px;
    background: linear-gradient(var(--color-principal) 0 0) 0 / 0% no-repeat #ddd;
    filter: blur(.5px);
    border: 4px solid;
    place-self: center;
    animation: 3.98s linear infinite p1;
  }
}

