.root-play {
    display: grid;
    grid-template-rows: 1fr 8% 1fr;
    min-width: 375px;
    width: 100%;
    height: 100vh;
    padding: 30px 30px;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media(min-width: 450px) {
    .root-play {
        justify-content: center;
        height: inherit;
        padding: 70px 55px;
        border: solid 4px #000;
        border-radius: 6px;
    }
}

@media(min-width: 1150px) {
    .root-play {
        justify-content: center;
        height: inherit;
        gap: 78px;
        padding: 30px 95px 5px;
        border: solid 6px #000;
        border-radius: 6px;
    }
}

.root-play-other {
    display: grid;
    grid-template-rows: 70% 1fr;
    gap: 28px;
    min-width: 375px;
    width: 100%;
    height: 100vh;
    padding: 100px 35px;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media(min-width: 450px) {
    .root-play-other {
        margin: auto 0;
        height: inherit;
        padding: 75px 55px 65px;
        border: solid 4px #000;
        border-radius: 6px;
    }
}

@media(min-width: 750px) {
    .root-play-other {
        margin: auto 0;
        height: inherit;
        padding: 65px 80px 55px;
        border: solid 4px #000;
        border-radius: 6px;
    }
}

@media(min-width: 950px) {
    .root-play-other {
        margin: auto 0;
        height: inherit;
        padding: 95px 140px 80px;
        border: solid 4px #000;
        border-radius: 6px;
    }
}

@media(min-width: 1150px) {
    .root-play-other {
        justify-content: center;
        height: inherit;
        gap: 108px;
        padding: 65px 95px 110px;
        border: solid 6px #000;
        border-radius: 6px;
        backdrop-filter: blur(3px);
    }
}

.root-play-reverse {
    display: grid;
    grid-template-rows: 1fr 8% 1fr;
    min-width: 375px;
    width: 100%;
    height: 100vh;
    padding: 30px 30px;
}

@keyframes fadeIn {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@media(min-width: 450px) {
    .root-play-reverse {
        justify-content: center;
        height: inherit;
        gap: 25px;
        padding: 70px 55px;
        border: solid 4px #000;
        border-radius: 6px;
    }
}

@media(min-width: 1150px) {
    .root-play-reverse {
        justify-content: center;
        height: inherit;
        gap: 98px;
        padding: 20px 95px 22px;
        border: solid 6px #000;
        border-radius: 6px;
        backdrop-filter: blur(3px);
    }
}

.container {
    display: flex;
    flex-direction: column;
    justify-self: center;
    align-self: center;
    width: 170px;
    height: 170px;
    border-radius: 70%;
    padding: 25px;
    box-shadow: 0 0 0 0 #0004;
    animation: pl1 1.5s infinite;
}

@keyframes pl1 {
    100% {
        box-shadow: 0 0 0 60px #0000
    }
}

.counter {
    justify-self: center;
    align-self: center;
}

.custom-plays,
.custom-plays-reverse {
    align-self: center;
}

.container-choose {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px 35px;
    background-color: var(--color-empate);
    border: 8px solid #000;
    border-radius: 6px;
}

.choose-text {
    font-family: 'Odibee Sans';
    font-weight: 400;
    font-size: 2.8rem;
    margin: 0;
    color: #fff;
}

.progress-bar {
    justify-self: center;
    align-self: center;
    width: 70%;
    height: 20px;
    background: linear-gradient(var(--color-principal) 0 0) 0/0% no-repeat #ddd;
    animation: p1 3.98s infinite linear;
}

@keyframes p1 {
    100% {
        background-size: 100%
    }
}

@media(min-width: 1150px) {
    .progress-bar {
        justify-self: center;
        align-self: center;
        width: 70%;
        height: 30px;
        background: linear-gradient(var(--color-principal) 0 0) 0/0% no-repeat #ddd;
        animation: p1 3.98s infinite linear;
        border: solid 4px;
        filter: blur(0.5px);
    }
}